import react from "react";

function Book({img,title,author}){
    // attribute, eventHandler
    // onClick, onMouse events

    const clickHandler = () => {
        alert ("Hellow World");
    }
    const complexExample = (author) =>{
        console.log(author);
    }
    return(
        <article className="book">
            <h1>{author}</h1>
            <h5>{title}</h5>
            <img src={img} alt=""/>
            
            <button onClick={clickHandler} type="button">Click Me</button>
            <button onClick={() => complexExample(author)} type="button">More</button>
        </article>
    );
}

export default Book
